import uniListItem from '@/components/uni-list-item/uni-list-item';
export default{
	components: {
		uniListItem
	},
	data(){
		return{
			userInfo:[],
		}
	},
	onLoad() {
		getApp().user_api(true);
	},
	onShow() {
		this.money();
	},
	onHide() {
		
	},
	methods:{
		money(){
			let self = this;
			let path = '/gzh/user_api/my'
			getApp().http(path, {}, function(res) {
				console.log('个人信息',res)
				self.userInfo = res;
			})
			console.log('是大风胜多负少的',self.userInfo)
		},
		navTo(e){
			uni.navigateTo({
				url: e,
			})
		},
	}
}
